// course-description styles
.node-type-course-description-page {
    #main {
        display: flex;
        flex-direction: column;

        @include tablet {
            flex-direction: row;
        }
    }

    .file-audio {
        min-height: 240px;
        position: relative;

        audio {
            @include absolute-center;
        }
    }

    .view-course-description-page-hero-banner-view-block- {
        @extend .container;

        min-height: 270px;
        padding: 0;

        video {
            margin: auto;
            width: 100%;
        }

        img {
            @extend .sr-only;
        }
    }

    .view-course-description-page-details-view-block- {
        background-color: #FFFFFF;
        box-shadow: 0 1px 29px 0 rgba(0, 0, 0, 0.16);
    }

    .course-description {
        &__header {
            margin-bottom: 30px;
            width: 100%;

            h1 {
                @include font(30);

                color: #14080A;
                font-weight: $bold;
                text-decoration: underline;
            }
        }

        &__controls {
            @include font(13);

            display: flex;
            align-items: center;
            justify-content: flex-end;
            margin-bottom: 20px;
        }

        &__lectures {
            margin-right: 50px;
        }

        &__time {
            background-color: #E8E8E8;
            border-radius: 20px;
            display: flex;
            margin-right: 20px;
            padding: 10px 15px;
            position: relative;

            &:before {
                @extend %icomoon;

                content: $icon-clock;
                color: $orange1;
                font-size: 17px;
                height: 17px;
                margin-right: 20px;
                width: 17px;
            }

            &:after {
                background-color: #BABABA;
                bottom: 0;
                content: '';
                display: block;
                height: 20px;
                margin: auto;
                position: absolute;
                left: 40px;
                top: 0;
                width: 2px;
            }
        }

        &__cta {
            background-color: transparent;
            border: 0;
            padding: 0;
            margin-right: 50px;
            text-decoration: underline;

            &:hover,
            &:focus {
                text-decoration: none;
            }
        }
    }

    .course_topics_main_heading {
        align-items: center;
        border-bottom: 1px solid #F5F5F5;
        display: flex;
        cursor: pointer;
        justify-content: space-between;
        padding: 20px;
        padding-left: 40px;
        position: relative;

        &:before,
        &:after {
            @include transition(transform, $animTime, $easeOutExpo);

            background-color: #14080A;
            content: '';
            display: block;
            height: 10px;
            left: 20px;
            position: absolute;
            transform: none;
            width: 2px;
        }

        &:after {
            transform: rotate(90deg);
        }

        &.-expanded {
            &:before,
            &:after {
                transform: rotate(270deg);
            }
        }
    }

    .field-name-field-course-topics {
        & > .field-items {
            & > .field-item {
                background-color: #FFFFFF;
                box-shadow: 0 1px 29px 0 rgba(0, 0, 0, 0.16);
                margin-bottom: 20px;

                &:only-child,
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        .field-name-field-title {
            @include font(18);

            color: #14080A;
            font-family: 'Crimson Text', serif;
            font-weight: $bold;
            line-height: 1;
            position: relative;
            top: 3px;
        }

        .sub_topics_resources {
            display: none;

            .field-items {
                & > .field-item {
                    margin-bottom: 0 !important;
                }
            }

            .content {
                display: flex;
                justify-content: space-between;
                padding: 20px;
            }
        }

        .field-name-field-sub-topic {
            a {
                @include font(15);

                color: #14080A;
                font-family: "Open Sans";
                line-height: 23px;
            }

            .field-items {
                & > .field-item {
                    margin-bottom: 20px;

                    &:only-child,
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
}

.page-open-learning {
    .page__title {
        margin: 50px 0;
        text-decoration: underline;
    }

    .view-view-course-listing {
        .view-content {
            @extend .clearfix;

            margin: 0 -10px;

            img {
                height: auto;
                width: 100%;
            }

            .views-row {
                min-height: 300px;
                padding: 0 10px;

                @include tablet {
                    float: left;
                    width: 33.333333%;
                }
            }

            .views-field,
            .field-content {
                min-height: 300px;
            }
        }
    }

    .course {
        min-height: 300px;

        img {
            @extend .sr-only;
        }

        &__item,
        &__wrap {
            min-height: 300px;
        }

        &__wrap {
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            position: relative;
        }

        &__time {
            @include font(13);

            background-color: #E8E8E8;
            border-radius: 20px;
            color: #222222;
            display: inline-block;
            margin-right: 20px;
            margin-bottom: 10px;
            padding: 10px 15px;
            position: relative;

            &:before {
                @extend %icomoon;

                content: $icon-clock;
                color: $orange1;
                font-size: 17px;
                height: 17px;
                margin-right: 20px;
                width: 17px;
            }

            &:after {
                background-color: #BABABA;
                bottom: 0;
                content: '';
                display: block;
                height: 20px;
                margin: auto;
                position: absolute;
                left: 40px;
                top: 0;
                width: 2px;
            }
        }

        &__detail {
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
            bottom: 0;
            color: #FFFFFF;
            padding: 20px;
            position: absolute;
            width: 100%;
        }

        &__title {
            @include font(24);

            color: #FFFFFF;
            display: block;
            font-family: "Crimson Text";
            font-weight: bold;

            &:hover,
            &:focus {
                color: #fff;
                text-decoration: none;
            }
        }

        &__desc {
            @include font(13);

            display: none;
        }

        &__summary {
            margin-bottom: 30px;
        }

        &__controls {
            border-top: 1px solid #DEDEDE;
            margin-top: 10px;
            padding: 10px 0;

            ul {
                display: flex;
            }

            li {
                margin-right: 10px;
            }

            button {
                text-decoration: underline;
            }

            a {
                color: #fff;
                display: inline-block;
                vertical-align: middle;

                &:before {
                    @extend %icomoon;

                    content: $icon-play-button;
                    display: inline-block;
                    font-size: 20px;
                    height: 20px;
                    margin-right: 10px;
                    position: relative;
                    top: 4px;
                    text-decoration: none;
                    width: 20px;
                }

                &:hover,
                &:focus {
                    text-decoration: none;
                }
            }
        }

        &__preview {
            display: none;
        }
    }
}
