// article styles
.not-front {
    background-color: #f5f5f5;
}

#main {
    @include clearfix;

    .not-front & {
        @extend .container;

        z-index: 10;
    }
}

.node-type-publication{
  #main{
    margin-top: -50px;

    @include breakpoint(medium){
      margin-top: 0px;
      margin-top: initial;
    }
  }
}

.node-publication.view-mode-full {
    margin-bottom: 50px;
    margin-top: 20px;

    @include tablet {
        margin-top: 50px;
        float: left;
        margin-left: 8.333333%;
        width: 50%;
    }


    p {
        @include font(17);

        color: #14080A;
        line-height: 32px;
        margin-bottom: 1em;
    }

    blockquote {
        @include font(24);

        background: #76716C url(../images/blockquote.jpg) no-repeat right center;
        background-size: cover;
        border-radius: 2px;
        margin: 0;
        margin-left: -100px;
        padding: 30px;

        p {
            color: $white;
        }
    }

    .field-name-body {
        @include tablet {
            padding-left: 75px;
        }
    }
}

.sidebars {
    @include tablet {
        float: left;
        margin-left: 8.333333%;
        width: 30%;
    }


    @include desktop {
        width: 20%;
    }
}
.node-type-publication {
  .main {
    article{
      ul{
        list-style: disc;
      }
      ol{
        list-style: decimal;
      }
      ul, ol{
        list-style-position: inside;
        line-height: 2rem;
      }
    }
  }
}

article.view-mode-full{
  table{
    font-size:1.13rem;
    line-height: 1.25;
    width:100%;
    max-width: 100%;
    border-collapse: collapse;
    border: 1px solid #14080A;
    border-spacing: 0;
    caption {
      font-weight: bold;
      padding: 10px 0;
    }

    th,
    td {
      border: 1px solid #14080A;
      padding: 5px;
      text-align: left;
      font-size: 1rem;
      vertical-align: middle;
    }
    // tbody tr:nth-child(odd) {
    //   background: #eee;
    // }
    }
}