// search styles
#page {
    position: relative;
    z-index: 5;
}

#block-views-exp-core-areas-of-study-page-1 {
    #header & {
        height: 50px;
        right: 0;
        position: absolute;
        top: 0;
        z-index: 5;
        width: 100%;

        @include large-desktop {
            left: -20px;
            right: -20px;
            width: 103.5%;
        }


        .block__content {
            @include large-desktop {
                margin-top: 70px;
            }
        }

        .search-form-widget {
            & > div {
                position: relative;
            }
        }

        .form-item {
            margin-top: 100px;
            width: 100%;

            @include large-desktop {
                margin-top: 0;
            }


            .form-text {
                @include font(28);

                background-color: transparent;
                border-radius: 0;
                border: 0;
                color: $white;
                font-family: 'Crimson Text', serif;
                font-weight: $bold;
                height: 100%;
                padding: 15px 40px;
                width: 100%;

                .not-front & {
                    border-bottom: 1px solid $yellow;

                    @include large-desktop {
                        border-bottom: 0;
                    }
                }
            }
        }

        .form-submit {
            background: url(../images/svg/search-white.svg) no-repeat center;
            background-size: 20px;
            border: 0;
            color: $white;
            font-size: 0;
            height: 49px;
            position: absolute;
            right: -40px;
            top: 0;
            width: 200px;
            border: 1px solid $white;


            @include large-desktop {
                background: rgba(0, 0, 0, 0.5) url(../images/svg/search-white.svg) no-repeat 160px center;
                background-size: 20px;
                right: -40px;
                top: 70px;
            }


            &:hover,
            &.-active {
                @include large-desktop {
                    background: $white url(../images/svg/search.svg) no-repeat 160px center;
                    background-size: 20px;
                }
            }
        }

        .views-exposed-widget {
            float: none;

            @include desktop {
                padding: 0 20px;
            }
        }

        .search__title {
            @include font(32);

            color: $white;
            font-family: 'Crimson Text', serif;
            font-weight: $bold;
            position: absolute;
            top: 50px;

            @include large-desktop {
                left: 20px;
                top: 20px;
            }
        }

        .search__close {
            @include font(16);

            color: $white;
            padding-right: 35px;
            text-decoration: none;
            top: 55px;
            position: absolute;
            right: -10px;

            @include large-desktop {
                top: 26px;
                right: 20px;
            }


            &:before,
            &:after {
                background-color: $white;
                border-radius: 2px;
                content: '';
                display: block;
                height: 4px;
                position: absolute;
                right: 0;
                top: 10px;
                width: 24px;
            }

            &:before {
                transform: rotate(45deg);
            }

            &:after {
                transform: rotate(-45deg);
            }
        }
    }
}

#views-exposed-form-core-areas-of-study-page-1 {
    .views-exposed-widget {
        .form-type-textfield {
            #header & {
                display: none;
            }
        }
    }
}

.autocomplete-suggestions {
    @include font(18);

    color: $white;
    font-family: 'Crimson Text', serif;
    margin-top: 2px;
    overflow: hidden;
    padding-left: 40px;

    @include desktop {
        @include font(24);
    }


    &.-sidebar {
        @include font(18);

        background-color: $white;
        border: 1px solid #bab8b5;
        color: #000;
        margin-top: -3px;
        padding: 5px 20px;

        .autocomplete-suggestion {
            margin: 10px 0;

            strong {
                color: #000;
                font-weight: $bold;
            }
        }
    }

    .autocomplete-suggestion {
        margin: 15px 0;

        strong {
            color: #faed95;
            font-weight: $normal;
        }
    }
}
