@keyframes rotating {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes pulse {
    10% {
        transform: scale(1);
    }

    11% {
        transform: scale(1.25);
    }

    12% {
        transform: scale(1);
    }
}

// header styles
.header {
    background-color: $background1;
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;
    position: relative;
    z-index: 10;
    top: 0;

    &:before,
    &:after {
        content: '';
        height: 100%;
        left: 0;
        position: absolute;

        // transition: all $animTime $easeInOutExpo;
        top: 0;
        width: 100%;
    }

    &:before {
        background: #635F5B;
        mix-blend-mode: darken;
        opacity: 0.5;
        z-index: 3;

        .isIE11 & {
            background: #862000;
        }
    }

    &:after {
        background: linear-gradient(-29deg, #D73704 65%, #EBA057 100%);
        z-index: 1;
    }

    .section-publications & {
        z-index: 1;
    }

    .not-front & {
        z-index: 10;

        @include tablet {
            z-index: 1;
        }

        &:before,
        &:after {
            // height: 50px;
            top: 0;

            @include large-desktop {
                height: 136px;
            }
            // @include breakpoint(custom, 516){
            // //   top: 113px;
            // }
        }

        &.-active {
            &:before,
            &:after {
                height: 507px;

                @include desktop {
                    height: 453px;
                }
            }

            .header__img {
                height: 551px;

                @include desktop {
                    height: 497px;
                }
            }
            z-index: 50 !important;
        }
    }

    .front & {
        height: 180px;

    }

    .section-contribute &,
    .page-core-areas-of-study & {
        z-index: 15;
    }

    &__logo,
    .block-superfish a {
        display: block;
        position: relative;
    }

    &__logo {
        margin: auto;
        width: 110px;
        z-index: 10;
        float: left;

        @include large-desktop {
            width: auto;
        }
    }

    &__inner {
        padding: 0 20px;
        position: relative;
        z-index: 5;
        top: 45px;
        @include large-desktop {
            border-bottom: 1px solid $yellow;
        }
        top: 0;

        > .container {
            padding: 10px 0;

            @include large-desktop {
                padding: 10px 0 0;
            }
        }

        svg {
            display: block;
            height: 30px;
            margin: auto;
            width: 110px;

            @include large-desktop {
                float: left;
                height: 62px;
                width: 215px;
            }
        }
    }

    &__img {
        background-size: cover;
        background-position: top center;
        background-repeat: no-repeat;
        display: block;
        height: 100%;
        left: 0;
        mix-blend-mode: overlay;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 2;

        .not-front & {

          height: 50px;

          @include large-desktop {
              height: 136px;
          }
        //   @include breakpoint(custom, 516){
        //     height: 163px;
        //   }
        }

        .isIE11 & {
            opacity: 0.25;
        }
    }

    .btn-explore {
        @include font(14);

        background-color: transparent;
        border: 0;
        bottom: 30px;
        color: $white;
        font-weight: $bold;
        left: 0;
        margin: auto;
        padding: 0;
        position: absolute;
        right: 0;
        text-align: center;
        width: 77px;
        z-index: 5;

        svg {
            display: inline-block;
            font-size: 65px;
            height: 65px;
            margin-bottom: 15px;
            width: 65px;
        }

        &:hover,
        &:focus {
            .line-top {
                transform: translateY(-25%);
            }

            .line-bottom {
                transform: translateY(25%);
            }

            .line-right {
                transform: translateX(25%);
            }

            .line-left {
                transform: translateX(-25%);
            }
        }
    }

    .circle1 {
        animation: pulse 45s linear infinite;
        transform-origin: center;
    }

    .circle2 {
        animation: rotating 30s linear infinite;
        transform-origin: center;
    }

    .line {
        @include transition(transform, $animTime, $easeOutExpo);

        position: relative;
        transform: none;
    }

    // Keeping this here to save the hassle of finding them all
    .header__inner .block-superfish a,
    .header__logo,
    #block-bean-homepage-hero-block .block__content .field-type-link-field a,
    #block-bean-homepage-hero-block .block__content .field-name-field-block-title,
    #block-bean-homepage-hero-block .block__content .field-name-field-text,
    .btn-explore {
        @include transition((background, opacity, transform, visibility), $animTime, $easeOutExpo);

        opacity: 1;
        transform: translateY(0);
        visibility: visible;
    }

    #block-views-exp-core-areas-of-study-page-1 .search__title,
    #block-views-exp-core-areas-of-study-page-1 .search__close {
        @include transition((background, opacity, transform, visibility), $animTime, $easeOutExpo);

        opacity: 0;
        transform: translateY(-20px);
        visibility: hidden;
    }

    &.-active {
        .header__inner .block-superfish a,
        .header__logo,
        #block-bean-homepage-hero-block .block__content .field-type-link-field a,
        #block-bean-homepage-hero-block .block__content .field-name-field-block-title,
        #block-bean-homepage-hero-block .block__content .field-name-field-text,
        .btn-explore {
            opacity: 0;
            transform: translateY(20px);
            visibility: hidden;
        }

        &:before {
            mix-blend-mode: multiply;
            opacity: 1;

            .isIE11 & {
                opacity: 0.75;
            }
        }

        #block-views-exp-core-areas-of-study-page-1 .search__title,
        #block-views-exp-core-areas-of-study-page-1 .search__close {
            opacity: 1;
            transform: translateY(0);
            visibility: visible;
        }

        .section-publications & {
            z-index: 2;
        }
    }
}

.view-standard-page-hero-image {
    @include clearfix;

    // height: 170px;

    @include tablet {
        height: 320px;
    }


    img {
        display: none;
    }

    .block-views-details-view-block {
        background-color: $white;
        box-shadow: 0 1px 29px 0 rgba(0, 0, 0, 0.16);
        float: right;
        padding: 20px;
        position: relative;
        right: -20px;
        width: 75%;

        @include tablet {
            display: none;
        }


        .-active & {
            display: none;
        }

        h2 {
            margin-top: 0;
        }

        .block-title {
            margin-top: 75px;

            @include desktop {
                margin-top: 50px;
            }
        }

        .view-content {
            @include font(13);

            color: #14080A;
        }

        .content-author,
        .publication-date {
            margin-bottom: 10px;
        }

        .views-row {
            > div > span {
                display: block;
                position: relative;
                width: 100%;
            }
        }
    }
}

.beta-banner {
    @include font(14);

    background-color: #e8e8e8;
    margin: 0 -20px;

    span {
        background-color: #D73704;
        border-radius: 4px;
        color: $white;
        display: inline-block;
        font-weight: bold;
        padding: 5px 10px;
    }

    a {
        color: #D73704;
        font-weight: $bold;
    }

    &__container {
        padding: 10px 20px;
    }
}

//universal defense header override
#global_nav_search{
  input{
    //fallback for IE
    height: 2rem;
    //preferred
    height: initial;
  }
}
#global_nav_main_wrapper{
  @include breakpoint(custom, 940){
    display: none;
  }
}
