#block-views-recently-forged-ideas-block,
#block-views-related-to-this-topic-block--2 {
    background: $background2;
    padding: 30px 0;

    .block-title {
        @include font(32);

        margin: 0 auto;
        text-align: center;
        padding: 0 0 30px 0;
    }

    .slick-list {
        padding: 0 20px !important;
        z-index: 10;

        @include desktop {
            padding: 0 246px !important;
        }
    }

    .slick-slide {
        width: 650px;
        padding: 0 20px;

        img {
            height: auto;
            width: 100%;
        }
    }

    .slick-arrow {
        background-color: #d73704;
        height: auto;
        padding: 14px;
        top: 70px;
        width: auto;
        z-index: 15;

        @include tablet {
            top: 140px;
        }


        @include desktop {
            background-color: #f5f5f5;
            padding: 18px;
            top: 140px;
        }


        &:before,
        &:after {
            @extend %icomoon;

            color: $white;
            font-size: 12px;
            height: 12px;
            width: 12px;

            @include desktop {
                color: #040000;
            }
        }

        &:hover,
        &:focus {
            color: $orange1;
        }
    }

    .slick-prev {
        left: 0;

        @include desktop {
            left: 152px;
        }


        &:before {
            content: "\e910";

            @include desktop {
                margin-right: 8px;
            }
        }
    }

    .slick-next {
        right: 0;

        @include desktop {
            right: 152px;
        }


        &:before {
            display: none;
        }

        &:after {
            content: "\e90a";

            @include desktop {
                margin-left: 8px;
            }
        }
    }

    .slick-page {
        color: #222;
        display: none;
        font-size: 12px;

        @include desktop {
            display: inline-block;
        }
    }

    .slick-dots {
        li {
            height: 14px;
            margin: 0 5px;
            width: 14px;
        }

        button {
            @include transition(background-color, $animTime, $easeOutExpo);

            background-color: transparent;
            border: 2px solid $orange1;
            border-radius: 50%;
            height: 14px;
            padding: 0;
            width: 14px;

            &:before {
                font-size: 0;
            }
        }

        .slick-active {
            button {
                background-color: $orange1;
            }
        }
    }

    .recently-desc {
        @include transition((opacity, visibility), $animTime, $easeOutExpo);

        // background: $white;
        // margin: -25% auto 30px;
        // max-width: 560px;
        // box-shadow: $box-shadow;
        // position: relative;
        // width: 90%;
        // z-index: 10;
        position: absolute;
        bottom: 0;
        padding: 0 30px;
        color: $white;

        @include tablet {
            opacity: 0;
            visibility: hidden;
        }

        @include large-desktop {
            margin: -20% auto 30px;
        }

        &:before {
            @include absolute-center;

            background: linear-gradient(to right, #d73704 60%,#e99650 100%);
            bottom: auto;
            content: '';
            height: 5px;
            width: 68px;
        }


        > div {
            @include font(13);
        }

        .field-name-field-duration {
            margin-bottom: 20px;
            color: #000;

            .field-items {
                background: #e8e8e8;
                border-radius: 20px;
                display: inline-block;
                padding: 10px 20px;
                width: auto;
            }
        }

        .content-title {
            @include transition(color, $animTime, $easeOutExpo);
            @include font(24);

            display: block;
            font-family: $font-title;
            font-weight: $bold;
            color: $white;
            margin-bottom: 15px;

            &:hover {
                color: $orange1;
            }
        }

        .field-name-field-date {
            display: inline-block;
            padding-right: 30px;
            vertical-align: top;

            &:before {
                @extend %icomoon;

                content: "\e90b";
                display: inline-block;
                font-size: 14px;
                height: 14px;
                vertical-align: top;
                margin-right: 10px;
                width: 14px;
            }

            .field-items {
                display: inline-block;
                vertical-align: top;
            }
        }

        .contributed-by {
            display: inline-block;
            margin-right: 4px;
            vertical-align: top;
        }

        .field-name-field-author {
            display: inline-block;
            vertical-align: top;
        }

        .field-name-body {
            margin: 20px 0 25px;
        }
        .field-name-field-image{
            background: #000;
        }

        .content-link {
            @include font(13);
            @include transition(color, $animTime, $easeOutExpo);

            &:before {
                @extend %icomoon;

                color: $orange1;
                content: "\e905";
                display: inline-block;
                font-size: 20px;
                height: 20px;
                margin-right: 10px;
                position: relative;
                text-decoration: none;
                top: 4px;
                width: 20px;
            }

            &:hover {
                color: $orange1;
            }
        }
    }

    .slick-current {
        .recently-desc {
            opacity: 1;
            visibility: visible;
        }
    }

    .field-name-field-duration {
        .field-items {
            &:before {
                @extend %icomoon;

                color: $orange1;
                display: inline-block;
                font-size: 20px;
                height: 20px;
                vertical-align: top;
                width: 20px;
            }
        }

        .field-item {
            border-left: 1px solid #bfbfbf;
            margin-left: 10px;
            padding: 1px 0 1px 10px;
            display: inline-block;
            vertical-align: top;
        }
    }

    .video {
        .field-name-field-duration {
            .field-items {
                &:before {
                    content: "\e90f";
                }
            }
        }
    }

    .podcast {
        .field-name-field-duration {
            .field-items {
                &:before {
                    content: "\e90d";
                }
            }
        }
    }

    .article {
        .field-name-field-duration {
            .field-items {
                &:before {
                    content: "\e90c";
                }
            }
        }
    }

    .slick-slide {
        a {
            display: block;
        }
    }
}
